html {
  overflow: hidden;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

ul,
ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}

form,
img {
  margin: 0;
  padding: 0;
}

img {
  border-width: 0;
}

textarea,
input {
  font-size: 14px;
}

select {
  margin: 0;
}

table {
  border-collapse: separate;
  border-spacing: 0;
}

td {
  padding: 0;
  margin: 0;
  border-width: 0;
}

pre {
  margin: 0;
  padding: 0;
}


label {
  display: block;
}

button, input, select, textarea {
  font-size: 100%;
  margin: 0;
}

button {
  padding: 0;
  cursor: pointer;
  line-height: normal;
  border: 0;
}

input[type=text] {
  border: 1px solid #c0c0c0;
}

